.page__footer {
    background-color:rgb(191,191,191);
    display: flex;
    flex-direction: column;
    align-items: center;
    position:absolute;
    bottom: 0px;
    left: 0px;
    width:100%;
    height:110px;
    @media all and (min-width:768px) {
      flex-direction: row;
      justify-content: space-between;
      align-items: center;
      height: 60px;
    }
  }
  
  .footer__copyright {
    color:#6b6b6b;
    font-family: 'Open Sans', sans-serif;
    margin:18px 61px 12px;
    font-size: 14px;
    @media all and (min-width:768px) {
      margin:0 41px;
    }
  }
  
  .footer__logo {
    width: 103px;
    height: 40px;
    margin:10px 41px;
  }